}
.ocado-layout-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hide {
  display: none;
}
.sectionTitle {
  margin-left: 10px;
  margin-top: 10px;
  display: inline-block;
  color: #ab6438;
}
.sectionSubtitle {
  display: inline-block;
  opacity: 0.5;
  font-size: 14px;
}
.recipe__suggestion {
  font-size: 24px !important;
  padding-left: 10px;
  padding-right: 10px;
}
.hero a {
  font-size: 1em;
  color: #fff;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  align-self: stretch;
  margin-bottom: 2em;
  justify-content: space-around;
  width: 50em;
}
.card {
  padding: 1em;
  padding-top: 0.5em;
  margin-bottom: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 230px;
  min-height: 230px;
  background-color: #fff;
}
.card__family {
  border-bottom: 1px solid #000;
  align-self: stretch;
  margin-bottom: 1em;
}
h3.card__family__name {
  font-size: 1.6rem;
  margin-bottom: 0;
}
.card__family__examples {
  font-style: italic;
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
  margin-top: -1em;
  margin-bottom: -0.5em;
}
.card__box {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
}
.card__box__title {
  margin-bottom: 4px;
}
.card__box--bought .card__box__items {
  font-style: italic;
}
.card__box--recommend {
  margin-bottom: 1em;
}
.item {
  display: flex;
  width: 100%;
  align-self: stretch;
  justify-content: space-between;
  margin-bottom: 4px;
  position: relative;
}
.item__name {
  padding: 2px;
  line-height: 28px;
}
.item__name__label {
  position: absolute;
  font-weight: 400;
  white-space: nowrap;
}
.item__spacer {
  flex: 1;
  background-color: #c1c1c1;
}
.item__score {
  align-self: flex-end;
  flex: 0 0 55px;
  padding: 0.5em;
  text-align: center;
  font-family: Source Code Pro;
  font-style: normal;
}
.item__text {
  max-width: 170px;
}
.item__star {
  align-self: flex-end;
  width: 30px;
  height: 30px;
  padding-right: 1.5em;
}
.scoresinfo .footer {
  padding: 2em;
}
.card--cruciferous .card__box--recommend .item__name {
  background-color: #00d260;
  color: #fff;
}
.card--cruciferous .card__box--bought .item__name {
  background-color: #9de8c4;
}
.card--green-leafy .card__box--recommend .item__name {
  background-color: #00d260;
  color: #fff;
}
.card--green-leafy .card__box--bought .item__name {
  background-color: #9de8c4;
}
.card--allium .card__box--recommend .item__name {
  background-color: #9a00fe;
  color: #fff;
}
.card--allium .card__box--bought .item__name {
  background-color: #c9a4ee;
}
.card--yellow-orange .card__box--recommend .item__name {
  background-color: #f39400;
  color: #fff;
}
.card--yellow-orange .card__box--bought .item__name {
  background-color: #fbd496;
}
.card--citrus .card__box--recommend .item__name {
  background-color: #f0e211;
  color: #000;
}
.card--citrus .card__box--bought .item__name {
  background-color: #f7f0c3;
}
.card--berry .card__box--recommend .item__name {
  background-color: #e70000;
  color: #fff;
}
.card--berry .card__box--bought .item__name {
  background-color: #faa9a9;
}
.onecard {
  display: flex;
  justify-content: center;
}
.onecard .card {
  padding: 1em;
  margin: 0;
  min-height: 0;
  flex: 1;
  width: auto;
}
@media (max-width: 50em) {
  .content {
    padding: 0;
  }
  .content .ocado-container {
    margin: 0;
  }
  .hero {
    display: none;
  }
  .hero-sticky-header {
    display: none;
  }
  .scores-container {
    display: none;
  }
  .card--yellow-orange .card__family__name {
    font-size: 1.4rem;
  }
}
